.aboutMain {
	height: 100vh;
	display: flex;
	width: 100%;
	justify-content: space-evenly;
	align-items: center;

	padding: 1em; /*Don't touch corners...*/
	border-bottom: 0.5em var(--primary-bgcolor-blue) solid;
}

.aboutSection {
	width: 50%;
	text-align: center;
}

.aboutSkills {
	width: 50%;
	text-align: center;
	padding-top: 20px;
}

.aboutSkills table {
	margin: auto;
}

/* Typography */
.aboutTitle {
	font-size: var(--fontsize-m);
}

/* Mobile - S(320), M(375px), L(425px)  */
@media screen and (max-width: 426px) {
	.aboutMain {
		height: auto;
		flex-direction: column;
	}

	.aboutSection {
		width: fit-content;
	}
}
